import * as React from "react";
import { useEffect, useState } from "react";
import { useMutationObserverRef } from "rooks";

import CookieConsent, {
  getCookieConsentValue,
  Cookies,
} from "react-cookie-consent";
import { initGA } from "./util/ga-utils";

import "./App.css";

import { createRootStore, StoreContext } from "./stores/RootStore";
import { DataProvider, useData } from "./components/DataContext";

import { CardFront } from "./components/CardFront";
import { CardBack } from "./components/CardBack";
import GBIcon from "./components/GBIcon";
import GBImages from "./components/GBImages";

const RootStore = createRootStore();

function App() {
  const handleAcceptCookie = () => {
    if (process.env.REACT_APP_GOOGLE_ANALYTICS_ID) {
      initGA(process.env.REACT_APP_GOOGLE_ANALYTICS_ID);
    }
  };
  const handleDeclineCookie = () => {
    Cookies.remove("_ga");
    Cookies.remove("_gat");
    Cookies.remove("_gid");
  };
  useEffect(() => {
    const isConsent = getCookieConsentValue();
    if (isConsent === "true") {
      handleAcceptCookie();
    }
  }, []);

  return (
    <StoreContext.Provider value={RootStore}>
      <DataProvider>
        <div className="App">
          <Main />
          <CookieConsent
            enableDeclineButton
            onAccept={handleAcceptCookie}
            onDecline={handleDeclineCookie}
          >
            <p>This site uses cookies to analyze traffic and use.</p>
            <p>
              Declining will not change your experience, but will opt-out of
              analytic data collection.
            </p>
          </CookieConsent>
        </div>
      </DataProvider>
    </StoreContext.Provider>
  );
}

function Main(props) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  return (
    <div className="Main">
      <header>
        <GuildList />
        <ModelLists />
      </header>
      <div className="Cards">
        {data.Models.map((m) => (
          <Content name={m.id} id={m.id} key={m.id} />
        ))}
      </div>
    </div>
  );
}

function SelectGuild({ name, minor }) {
  document
    .querySelectorAll(".model-checkbox")
    .forEach((m) => m.classList.add("hide"));

  let e = document.querySelector(".model-list-container");
  if (minor) {
    e.style.setProperty("--major-order", 2);
    e.style.setProperty("--minor-order", 0);
  } else {
    e.style.setProperty("--major-order", 0);
    e.style.setProperty("--minor-order", 2);
  }

  document
    .querySelectorAll(`.model-checkbox.${name}`)
    .forEach((m) => m.classList.remove("hide"));
}

function GuildList(props) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        display: "grid",
        gap: "2px",
        // gridTemplateColumns: "repeat(4, 1fr)",
        gridTemplateRows: "repeat(7, 1fr)",
        gridAutoFlow: "column",
      }}
    >
      {data.Guilds.map((g) => (
        <GuildListItem key={g.name} g={g} />
      ))}
    </div>
  );
}

function GuildListItem({ g }) {
  return (
    <div
      className="guild"
      key={g.name}
      onClick={() => {
        SelectGuild(g);
      }}
      style={{ "--guild-color": g.shadow ?? g.color }}
    >
      <span style={{ display: "inline-flex" }}>
        <div
          style={{
            backgroundColor: "black",
            fontSize: "2em",
            width: "1em",
            height: "1em",
            borderRadius: "1em",
            display: "flex",
            overflow: "visible",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <GBIcon
            icon={g.name}
            style={{
              // FIXME, this is only working if the
              // svg doesn't have fill color
              color: g.darkColor ?? g.color,
              flexShrink: 0,
            }}
          />
        </div>
        <span
          style={{
            alignSelf: "center",
            marginLeft: "1em",
            marginRight: "1em",
          }}
        >
          {g.name}
        </span>
      </span>
    </div>
  );
}

function DisplayModel(name) {
  var check = document.getElementById(name + "-check");
  var card = document.querySelector(`.card#${name}`);

  // card.classList.toggle("hide");
  if (check.checked === true) {
    card.classList.remove("hide");
  } else {
    card.classList.add("hide");
  }
}

function ModelLists(props) {
  const { data, loading } = useData();
  if (loading) {
    return null;
  }
  return (
    <div
      className="model-list-container"
      style={{ width: "100%", "--major-order": 0, "--minor-order": 2 }}
    >
      {data.Models.map((m) => {
        const guild1 = data.Guilds.find((g) => g.name === m.guild1);
        const guild2 = data.Guilds.find((g) => g.name === m.guild2);
        return (
          <label
            className={`model-checkbox ${m.guild1} ${m.guild2} ${m.id} hide ${
              guild1.minor ? "minor" : ""
            }`}
            key={m.id}
            style={{
              "--color1": guild1.shadow ?? guild1.color + "80",
              "--color2": guild2
                ? guild2.shadow ?? guild2.color + "80"
                : "var(--color1)",
              // backgroundColor: guild1.shadow ?? guild1.color + "80",
            }}
          >
            <input
              type="checkbox"
              name={m.id}
              id={`${m.id}-check`}
              key={m.id}
              onChange={() => DisplayModel(m.id)}
            />
            {m.id}
          </label>
        );
      })}
    </div>
  );
}

function Content({ name, guild, id }) {
  const { data, loading } = useData();

  const [inView, setInView] = useState(false);
  const callback = (mutationList, observer) => {
    if (mutationList && mutationList[0]) {
      let { target } = mutationList[0];
      let style = getComputedStyle(target);
      setInView(style.getPropertyValue("display") !== "none");
    }
  };
  const [ref] = useMutationObserverRef(callback);

  if (loading) {
    return null;
  }
  const model = data.Models.find((m) => m.id === name);

  if (GBImages[`${model.id}_gbcp_front`]) {
    model.gbcp = true;
  }

  return (
    <div
      ref={ref}
      className={`card ${!inView ? "hide" : null}`}
      id={id}
      style={{
        width: "5in",
        height: "3.5in",
      }}
    >
      {inView && (
        <>
          <CardFront
            model={model}
            style={{
              maxWidth: "2.5in",
              borderRadius: 0,
              "--scale": "calc(2.5 * 96 / 500)",
            }}
          />
          <CardBack
            model={model}
            style={{
              maxWidth: "2.5in",
              borderRadius: 0,
              "--scale": "calc(2.5 * 96 / 500)",
            }}
          />
        </>
      )}
    </div>
  );
}

export default App;
